* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #0d1421;
  color: #ffffff;
  min-height: 100vh;
}

.app {
  min-height: 100vh;
  background: linear-gradient(135deg, #0d1421 0%, #1a2332 100%);
}

/* Header */
.header {
  background: rgba(13, 20, 33, 0.95);
  backdrop-filter: blur(20px);
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  position: sticky;
  top: 0;
  z-index: 100;
}

.header-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 24px;
  height: 72px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.logo {
  display: flex;
  align-items: center;
  gap: 12px;
  font-size: 24px;
  font-weight: 700;
}

.logo-icon {
  font-size: 28px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.logo-text {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.nav {
  display: flex;
  gap: 32px;
}

.nav-link {
  color: #8892b0;
  text-decoration: none;
  font-weight: 500;
  padding: 8px 16px;
  border-radius: 8px;
  transition: all 0.2s ease;
}

.nav-link:hover {
  color: #ffffff;
  background: rgba(255, 255, 255, 0.05);
}

.nav-link.active {
  color: #667eea;
  background: rgba(102, 126, 234, 0.1);
}

.header-actions {
  display: flex;
  align-items: center;
  gap: 16px;
}

.settings-btn {
  background: none;
  border: none;
  color: #8892b0;
  font-size: 20px;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.2s ease;
}

.settings-btn:hover {
  color: #ffffff;
  background: rgba(255, 255, 255, 0.05);
}

.wallet-info {
  display: flex;
  align-items: center;
  gap: 12px;
}

.wallet-address {
  background: rgba(102, 126, 234, 0.1);
  color: #667eea;
  padding: 8px 16px;
  border-radius: 12px;
  font-weight: 500;
  font-size: 14px;
}

.connect-btn, .disconnect-btn {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: none;
  padding: 12px 24px;
  border-radius: 12px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
}

.connect-btn:hover, .disconnect-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(102, 126, 234, 0.3);
}

.disconnect-btn {
  background: rgba(255, 255, 255, 0.1);
  font-size: 14px;
  padding: 8px 16px;
}

/* Main Content */
.main {
  max-width: 1200px;
  margin: 0 auto;
  padding: 40px 24px;
}

.swap-container {
  display: grid;
  grid-template-columns: 1fr 300px;
  gap: 32px;
  align-items: start;
}

.swap-card {
  background: rgba(26, 35, 50, 0.8);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 24px;
  padding: 32px;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
}

.swap-header {
  margin-bottom: 32px;
}

.swap-header h2 {
  font-size: 28px;
  font-weight: 700;
  margin-bottom: 8px;
  background: linear-gradient(135deg, #ffffff 0%, #8892b0 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.swap-info {
  color: #8892b0;
  font-size: 16px;
}

.swap-form {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.token-input {
  background: rgba(13, 20, 33, 0.6);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 16px;
  padding: 20px;
  transition: all 0.2s ease;
}

.token-input:hover {
  border-color: rgba(102, 126, 234, 0.3);
}

.token-input-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 12px;
  font-size: 14px;
  color: #8892b0;
}

.balance {
  cursor: pointer;
  transition: color 0.2s ease;
}

.balance:hover {
  color: #667eea;
}

.token-input-content {
  display: flex;
  align-items: center;
  gap: 16px;
}

.amount-input {
  flex: 1;
  background: none;
  border: none;
  color: #ffffff;
  font-size: 24px;
  font-weight: 600;
  outline: none;
}

.amount-input::placeholder {
  color: #8892b0;
}

.token-select {
  display: flex;
  align-items: center;
  gap: 8px;
  background: rgba(102, 126, 234, 0.1);
  border: 1px solid rgba(102, 126, 234, 0.2);
  border-radius: 12px;
  padding: 12px 16px;
  color: #ffffff;
  cursor: pointer;
  transition: all 0.2s ease;
  min-width: 120px;
}

.token-select:hover {
  background: rgba(102, 126, 234, 0.2);
  border-color: rgba(102, 126, 234, 0.4);
}

.token-logo {
  font-size: 20px;
}

.token-symbol {
  font-weight: 600;
  font-size: 16px;
}

.dropdown-arrow {
  font-size: 12px;
  color: #8892b0;
}

.swap-arrow-container {
  display: flex;
  justify-content: center;
  margin: -8px 0;
  position: relative;
  z-index: 10;
}

.swap-arrow {
  background: rgba(26, 35, 50, 0.9);
  border: 2px solid rgba(102, 126, 234, 0.3);
  border-radius: 12px;
  width: 48px;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #667eea;
  font-size: 20px;
  cursor: pointer;
  transition: all 0.2s ease;
}

.swap-arrow:hover {
  background: rgba(102, 126, 234, 0.1);
  border-color: rgba(102, 126, 234, 0.5);
  transform: rotate(180deg);
}

.swap-details {
  background: rgba(13, 20, 33, 0.4);
  border-radius: 12px;
  padding: 16px;
  margin: 16px 0;
}

.swap-rate {
  color: #ffffff;
  font-weight: 500;
  margin-bottom: 8px;
}

.swap-slippage {
  color: #8892b0;
  font-size: 14px;
  margin-bottom: 4px;
}

.swap-impact {
  color: #4ade80;
  font-size: 14px;
}

.swap-btn {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: none;
  padding: 18px 32px;
  border-radius: 16px;
  font-size: 18px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
  margin-top: 16px;
}

.swap-btn:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 12px 30px rgba(102, 126, 234, 0.4);
}

.swap-btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.swap-btn.connect {
  background: linear-gradient(135deg, #f093fb 0%, #f5576c 100%);
}

/* Market Info */
.market-info {
  background: rgba(26, 35, 50, 0.8);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 24px;
  padding: 24px;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
}

.market-info h3 {
  font-size: 20px;
  font-weight: 700;
  margin-bottom: 20px;
  color: #ffffff;
}

.market-stats {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.stat {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.05);
}

.stat:last-child {
  border-bottom: none;
}

.stat-label {
  color: #8892b0;
  font-size: 14px;
}

.stat-value {
  color: #ffffff;
  font-weight: 600;
  font-size: 16px;
}

/* Wallet Balances */
.wallet-balances {
  margin-top: 24px;
  padding-top: 24px;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}

.wallet-balances h4 {
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 16px;
  color: #ffffff;
}

.balance-list {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.balance-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px 0;
  font-size: 14px;
}

.balance-item span:first-child {
  color: #8892b0;
}

.balance-item span:last-child {
  color: #ffffff;
  font-weight: 500;
}

/* Modal */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(8px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  padding: 20px;
}

.modal {
  background: rgba(26, 35, 50, 0.95);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 24px;
  width: 100%;
  max-width: 480px;
  max-height: 80vh;
  overflow: hidden;
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.5);
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 24px 32px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.modal-header h3 {
  font-size: 20px;
  font-weight: 700;
  color: #ffffff;
}

.modal-close {
  background: none;
  border: none;
  color: #8892b0;
  font-size: 24px;
  cursor: pointer;
  padding: 4px;
  border-radius: 8px;
  transition: all 0.2s ease;
}

.modal-close:hover {
  color: #ffffff;
  background: rgba(255, 255, 255, 0.1);
}

.token-search {
  padding: 24px 32px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.search-input {
  width: 100%;
  background: rgba(13, 20, 33, 0.6);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  padding: 16px;
  color: #ffffff;
  font-size: 16px;
  outline: none;
  transition: all 0.2s ease;
}

.search-input:focus {
  border-color: rgba(102, 126, 234, 0.5);
}

.search-input::placeholder {
  color: #8892b0;
}

.search-help {
  margin-top: 8px;
  font-size: 12px;
  color: #8892b0;
  text-align: center;
}

.address-validation {
  margin-top: 12px;
}

.validation-success {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 8px 12px;
  background: rgba(74, 222, 128, 0.1);
  border: 1px solid rgba(74, 222, 128, 0.3);
  border-radius: 8px;
  color: #4ade80;
  font-size: 14px;
}

.validation-error {
  padding: 8px 12px;
  background: rgba(248, 113, 113, 0.1);
  border: 1px solid rgba(248, 113, 113, 0.3);
  border-radius: 8px;
  color: #f87171;
  font-size: 14px;
  text-align: center;
}

.add-token-btn {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: none;
  padding: 6px 12px;
  border-radius: 6px;
  font-weight: 600;
  cursor: pointer;
  font-size: 12px;
  transition: all 0.2s ease;
}

.add-token-btn:hover {
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(102, 126, 234, 0.3);
}

.token-list {
  max-height: 400px;
  overflow-y: auto;
  padding: 16px 0;
}

.token-item {
  width: 100%;
  background: none;
  border: none;
  color: #ffffff;
  padding: 16px 32px;
  display: flex;
  align-items: center;
  gap: 16px;
  cursor: pointer;
  transition: all 0.2s ease;
}

.token-item:hover {
  background: rgba(102, 126, 234, 0.1);
}

.token-item .token-logo {
  font-size: 24px;
  width: 32px;
  text-align: center;
}

.token-info {
  flex: 1;
  text-align: left;
}

.token-info .token-symbol {
  display: block;
  font-weight: 600;
  font-size: 16px;
  margin-bottom: 4px;
}

.token-info .token-name {
  display: block;
  color: #8892b0;
  font-size: 14px;
}

.token-balance {
  color: #8892b0;
  font-size: 14px;
}

/* Settings */
.settings-content {
  padding: 24px 32px;
}

.setting-group {
  margin-bottom: 24px;
}

.setting-group:last-child {
  margin-bottom: 0;
}

.setting-group label {
  display: block;
  color: #ffffff;
  font-weight: 600;
  margin-bottom: 12px;
}

.slippage-options {
  display: flex;
  gap: 8px;
  align-items: center;
}

.slippage-btn {
  background: rgba(13, 20, 33, 0.6);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 8px;
  padding: 8px 16px;
  color: #8892b0;
  cursor: pointer;
  transition: all 0.2s ease;
}

.slippage-btn:hover {
  border-color: rgba(102, 126, 234, 0.3);
  color: #ffffff;
}

.slippage-btn.active {
  background: rgba(102, 126, 234, 0.2);
  border-color: rgba(102, 126, 234, 0.5);
  color: #667eea;
}

.custom-slippage {
  background: rgba(13, 20, 33, 0.6);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 8px;
  padding: 8px 12px;
  color: #ffffff;
  width: 80px;
  outline: none;
  transition: all 0.2s ease;
}

.custom-slippage:focus {
  border-color: rgba(102, 126, 234, 0.5);
}

.rpc-input {
  width: 100%;
  background: rgba(13, 20, 33, 0.6);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  padding: 16px;
  color: #ffffff;
  font-size: 14px;
  outline: none;
  transition: all 0.2s ease;
}

.rpc-input:focus {
  border-color: rgba(102, 126, 234, 0.5);
}

.rpc-input::placeholder {
  color: #8892b0;
}

/* Responsive */
@media (max-width: 768px) {
  .header-content {
    padding: 0 16px;
    height: 64px;
  }
  
  .nav {
    display: none;
  }
  
  .logo {
    font-size: 20px;
  }
  
  .main {
    padding: 20px 16px;
  }
  
  .swap-container {
    grid-template-columns: 1fr;
    gap: 24px;
  }
  
  .swap-card {
    padding: 24px;
  }
  
  .modal {
    margin: 20px;
    max-width: none;
  }
  
  .modal-header,
  .token-search,
  .settings-content {
    padding: 20px 24px;
  }
  
  .token-item {
    padding: 16px 24px;
  }
}

@media (max-width: 480px) {
  .header-content {
    padding: 0 12px;
  }
  
  .main {
    padding: 16px 12px;
  }
  
  .swap-card {
    padding: 20px;
  }
  
  .amount-input {
    font-size: 20px;
  }
  
  .token-select {
    min-width: 100px;
    padding: 10px 12px;
  }
  
  .swap-btn {
    padding: 16px 24px;
    font-size: 16px;
  }
}

/* Scrollbar */
::-webkit-scrollbar {
  width: 6px;
}

::-webkit-scrollbar-track {
  background: rgba(13, 20, 33, 0.3);
}

::-webkit-scrollbar-thumb {
  background: rgba(102, 126, 234, 0.3);
  border-radius: 3px;
}

::-webkit-scrollbar-thumb:hover {
  background: rgba(102, 126, 234, 0.5);
} 